.form__radio-group {
  width: 100%;
  display: inline-block; }

.form__radio-label {
  position: relative;
  padding-left: 2.5rem; }

.form__radio-input {
  display: none; }

.form__radio-button {
  height: 1.7rem;
  width: 1.7rem;
  border-radius: 50%;
  border: 2px solid #A7A7A8;
  display: inline-block;
  position: absolute;
  top: 0.1rem;
  left: 0; }
  .form__radio-button::after {
    content: '';
    display: block;
    height: 0.875rem;
    width: 0.875rem;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #3E6EB8;
    opacity: 0;
    transition: opacity .2s; }

.form__radio-input:checked ~ .form__radio-label .form__radio-button::after {
  opacity: 1; }

.form [type="checkbox"]:checked + label:after {
  opacity: 0; }

/* Base for label styling */
[type="checkbox"]:not(:checked),
[type="checkbox"]:checked {
  position: absolute;
  left: -9999px; }

[type="checkbox"]:not(:checked) + label,
[type="checkbox"]:checked + label {
  position: relative;
  padding-left: 1.95rem;
  cursor: pointer; }

/* checkbox aspect */
[type="checkbox"]:not(:checked) + label:before,
[type="checkbox"]:checked + label:before {
  content: '';
  position: absolute;
  left: -5px;
  top: 2px;
  width: 1.4rem;
  height: 1.4rem;
  background: #fff;
  border-radius: 2px; }

/* checked mark aspect */
[type="checkbox"]:not(:checked) + label:after,
[type="checkbox"]:checked + label:after {
  content: '\2713';
  position: absolute;
  top: 0.45rem;
  left: -0.08rem;
  font-size: 1rem;
  line-height: 0.8;
  color: #09ad7e;
  transition: all .2s; }

/* checked mark aspect changes */
[type="checkbox"]:not(:checked) + label:after {
  opacity: 0;
  transform: scale(0); }

[type="checkbox"]:checked + label:after {
  opacity: 1;
  transform: scale(1); }

/* disabled checkbox */
[type="checkbox"]:disabled:not(:checked) + label:before,
[type="checkbox"]:disabled:checked + label:before {
  box-shadow: none;
  border-color: #bbb;
  background-color: #ddd; }

[type="checkbox"]:disabled:checked + label:after {
  color: #999; }

[type="checkbox"]:disabled + label {
  color: #aaa; }

